
h1,
h2,
h3,
p {
    margin-top: 0;
    margin-bottom: 0;
    padding-top: 0;
    padding-bottom: 0;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
    margin: 0;
}

.list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.link {
    text-decoration: none;
    color: inherit;
}
.gallery
 {
  width:100%;
  padding: 20px;
  margin:0 auto;

  @media screen and(min-width: 320px) {
    width: 320px;
    padding: 20px;
  }
  @media screen and(min-width: 768px) {
    width: 768px;
    padding: 32px;
  }
  @media screen and(min-width: 1280px) {
    width: 1280px;
    padding: 32px;
    }
}


    
    .gallery__list{
        
        display: grid;
        margin-top: 32px;
        margin-bottom: 32px;
        margin-left: auto;
        margin-right: auto;
        grid-auto-rows: 443px;
        grid-gap: 20px;
        
        @media screen and (min-width: 768px) {
            grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
            grid-auto-rows: auto;
            grid-gap: 32px;
        }
        @media screen and (min-width: 1280px) {
            grid-gap: 32px 16px;         }
    }

    .gallery__item{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 100%;
        border-radius: 5px;
        transition: all 250ms;
        cursor: pointer;
        &:hover {
            box-shadow: 0 4px 10px #000;
            
        }
        
        

        @media screen and (min-width: 768px) {
            width: 336px;
            height: 494px;
            
        }

        @media screen and (min-width: 1280px) {
            width: 395px;
            height: 634px;
            
        }


        &-image {
           
            border-radius: 5px;
            width: 280px;
            height: 402px;

            @media screen and (min-width: 768px) {
                width: 336px;
                height: 455px;
            }

            @media screen and (min-width: 1280px) {
                width: 395px;
                height: 574px;
                border-radius: 5px;
            }
        }

    

    

    &-description {
        font-family: 'Roboto', sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 1.33;
        width: 280px;
        margin-top: 10px;
        

        @media screen and (min-width: 768px) {
            width: 336px;
            display: flex;
            flex-direction: column;
            flex-grow: 1;
            margin-top: 8px;
        }

        @media screen and (min-width: 1280px) {
            font-size: 20px;
            line-height: 1.2;
            width: 395px;
            margin-top: 12px;
            
        }
       &-title {
            overflow: hidden;
            display: -webkit-box;
           -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            text-transform: uppercase;
            color: #000000;
        }

        &-genres {
            text-transform: capitalize;
            color: #B92F2C;
        }
    }}
